footer {
   background: #fff9f8;
}
.footer {
   display: flex;
   justify-content: space-between;
   align-items: flex-start;
   padding: 100px 0;
   @media (max-width: 992px) {
      flex-direction: column;
   }
   @media (max-width: 992px) {
      padding: 51px 0 32px;
   }
   &__content {
      @media (max-width: 992px) {
         display: flex;
         width: 100%;
         justify-content: space-between;
         align-items: center;
         margin-bottom: 30px;
         order: 1;
      }
   }
   &__logo {
      width: auto;
      margin-bottom: 80px;
      @media (max-width: 992px) {
         margin-bottom: 0;
      }
      img {
         width: 100%;
         height: auto;
      }
   }
   &__icons {
      display: flex;
      align-items: center;
      li {
         margin-right: 30px;
         @media (max-width: 480px) {
            margin-right: 20px;
         }
         &:last-child {
            margin-right: 0;
         }
      }
      @media (max-width: 480px) {
         margin-left: 20px;
      }
   }
   &__icon {
      img {
         height: 25px;
         width: auto;
         @media (max-width: 480px) {
            height: 17px;
         }
      }
   }
   &__menu {
      display: flex;
      align-items: flex-start;
      @media (max-width: 992px) {
         order: 3;
         width: 100%;
         justify-content: center;
         margin-bottom: 40px;
      }
   }
   &__list {
      margin-right: 114px;
      li {
         margin-bottom: 20px;
         @media (max-width: 480px) {
            margin-bottom: 15px;
         }
         &:last-child {
            margin-bottom: 0;
         }
      }
      &:last-child {
         margin-right: 0;
      }
      @media (max-width: 1110px) {
         margin-right: 40px;
      }
      @media (max-width: 992px) {
         margin-right: 64px;
      }
   }
   &__list-link {
      font-weight: 300 !important;
   }
   &__contact {
      display: flex;
      flex-direction: column;
      @media (max-width: 992px) {
         width: 100%;
         order: 2;
         align-items: flex-start;
         flex-wrap: wrap;
         flex-direction: row;
         .footer__link {
            display: none;
         }
      }
   }
   &__title {
      font-weight: 600 !important;
      color: #8b746d !important;
      margin-bottom: 5px;
      @media (max-width: 992px) {
         flex: 0 1 100%;
      }
   }
   &__select {
      margin-bottom: 25px;
      @media (max-width: 992px) {
         flex: 0 1 50%;
         margin-bottom: 0;
      }
   }
   &__contact-content {
      margin-bottom: 48px;
      @media (max-width: 992px) {
         display: flex;
         width: 100%;
         flex-wrap: wrap;
         justify-content: space-between;
         .footer__link {
            flex-wrap: 0 1 50%;
            display: inline-flex;
         }
      }
   }
   &__link {
      display: inline-flex;
      align-items: center;
      margin-bottom: 20px;
      &:last-child {
         margin-bottom: 0;
      }
      img {
         margin-right: 5px;
      }
      @media (max-width: 992px) {
         flex: 0 1 50%;
         align-items: flex-start;
         img {
            margin-top: 2px;
         }
         &:last-child {
            justify-content: flex-end;
         }
      }
   }
   &__contact-mob {
      display: none;
      @media (max-width: 992px) {
         display: flex;
         justify-content: space-between;
         align-items: flex-start;
         width: 100%;
         order: 4;
         padding-top: 30px;
         border-top: 1px solid #dad3cc;
      }
   }
   .select {
      position: relative;
      width: 100%;
      select {
         display: none;
      }
      &-selected {
         border-bottom: 1px solid #dad3cc;
         padding-bottom: 7px;
         &::after {
            content: url("..//img/footer-arrow-down.svg");
            position: absolute;
            right: 0;
            top: -30%;
            transform: translate(0, -50%);
         }
      }
      &-arrow:after {
         transform: translate(0, -50%) rotate(180deg);
      }
      div,
      &-selected {
         cursor: pointer;
         user-select: none;
      }
      &-items {
         position: absolute;
         top: 100%;
         left: 50%;
         width: 100%;
         transform: translate(-50%, 0);
         z-index: 99 !important;
         background: #fff9f8;
         border: 1px solid #dad3cc;
         padding: 10px;
         div {
            margin-bottom: 5px;
            &:last-child {
               margin-bottom: 0;
            }
         }
      }
      &-hide {
         display: none;
      }
   }
}
