.section {
   &__stocks {
      padding: 120px 0 0;
      &-discount {
         padding: 80px 0 120px;
         @media (max-width: 768px) {
            padding: 50px 0 70px !important;
         }
         .stocks {
            &__items {
               display: grid;
               grid-template-columns: repeat(2, 1fr);
               column-gap: 28px;
               row-gap: 74px;
               @media (max-width: 768px) {
                  gap: 28px;
               }
               @media (max-width: 600px) {
                  grid-template-columns: repeat(1, 1fr);
                  gap: 15px;
               }
            }
            &__content {
               justify-content: center;
            }
         }
      }
   }
}
.stocks__pagination {
   background: #fcfbfa;
   border-radius: 25px;
   display: flex;
   justify-content: center;
   align-items: center;
   padding: 10px 20px;
   &-item {
      margin-right: 20px;
      a {
         color: #8b746d !important;
      }
      &.active a {
         color: #7a5da8 !important;
      }
      &:last-child {
         margin-right: 0;
      }
   }
}
.stocks {
   .swiper-pagination {
      display: none;
      @media (max-width: 768px) {
         display: flex;
         justify-content: center;
         align-items: center;
         position: relative;
         bottom: 0;
         right: 0;
         left: 0;
      }
      &-bullet {
         background: #cbc0dd;
         width: 5px;
         height: 5px;
         &-active {
            width: 9px;
            height: 9px;
            background: #7a5da8;
         }
      }
   }
   &__swiper {
      margin-bottom: 50px;
      @media (max-width: 768px) {
         margin-bottom: 30px;
      }
      .slide {
         background: #fff9f8;
         border-radius: 25px 25px 25px 0px;
         &__img {
            width: 100%;
            height: auto;
            overflow: hidden;
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
         }
         &__content {
            padding: 20px 30px;
            @media (max-width: 992px) {
               padding: 10px 15px;
            }
         }
         &__title {
            font-family: "Gilroy ExtraBold" !important;
            color: #2e1a1e !important;
         }
      }
   }
   &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 768px) {
         justify-content: center;
         .swiper-buttons {
            display: none;
         }
      }
   }
   &__btn {
      @media (max-width: 768px) {
         display: none !important;
      }
   }
}
