header {
   background: #fff9f8;
}
.nav {
   padding: 27px 0;
   @media (max-width: 992px) {
      padding: 17px 0;
      &.active {
         position: fixed;
         background: #fff9f8;
         left: 0;
         right: 0;
         top: 0;
         padding: 17px 25px;
         z-index: 99;
         .nav__top {
            max-width: 750px;
            margin: 0 auto;
         }
      }
   }
   @media (max-width: 600px) {
      padding: 6px 0;
      &.active {
         padding: 6px 25px 51px;
      }
   }
   @media (max-width: 480px) {
      padding: 6px 0;
      &.active {
         padding: 6px 15px 51px;
      }
   }
   &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 27px;
      @media (max-width: 992px) {
         margin-bottom: 0;
      }
   }
   &__logo {
      margin-right: 80px;
      img {
         width: 100%;
      }
      @media (max-width: 1280px) {
         margin-right: 20px;
         width: 140px;
      }
      @media (max-width: 600px) {
         margin-right: auto;
         width: 110px;
      }
   }
   &__location {
      margin-right: 80px;
      display: flex;
      align-items: center;
      cursor: pointer;
      img {
         margin-right: 5px;
      }
      &-mobile {
         display: none;
      }
      @media (max-width: 1280px) {
         margin-right: 20px;
      }
      @media (max-width: 992px) {
         margin-right: auto;
      }
      @media (max-width: 600px) {
         margin-left: auto;
         margin-right: 32px;
         img,
         .nav__top-text {
            display: none;
         }
         &-mobile {
            display: block !important;
         }
      }
      @media (max-width: 350px) {
         margin-right: 15px;
      }
   }
   &__top-text {
      position: relative;
      &::after {
         content: "";
         position: absolute;
         width: 100%;
         left: 0;
         bottom: 0;
         height: 1px;
         border: 1px dashed #7a5da8;
      }
   }
   &__work {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      margin-right: auto;
      @media (max-width: 992px) {
         display: none;
      }
   }
   &__work-title {
      margin-bottom: 5px;
   }
   &__top-link {
      margin-right: 90px;
      cursor: pointer;
      img {
         display: none;
      }
      @media (max-width: 1280px) {
         margin-right: 20px;
      }
      @media (max-width: 600px) {
         span {
            display: none;
         }
         img {
            display: block;
            margin-right: 32px;
         }
      }
   }
   &__top-btn {
      padding: 13px 35px !important;
      @media (max-width: 992px) {
         display: none;
      }
   }
   .select {
      position: relative;
      width: auto;
      padding-right: 20px;
      margin-left: 54px;
      @media (max-width: 1280px) {
         margin-left: 20px;
      }
      @media (max-width: 600px) {
         display: none;
      }
      select {
         display: none;
      }
      &-selected {
         padding-bottom: 5px;
         border-bottom: 1px solid #dad3cc;
         &::after {
            content: url("..//img/arrow-down.svg");
            position: absolute;
            right: 0;
            top: 30%;
            transform: translate(0, -50%);
         }
      }
      &-arrow:after {
         transform: translate(0, -30%) rotate(180deg);
      }
      div,
      &-selected {
         cursor: pointer;
         user-select: none;
      }
      &-items {
         position: absolute;
         top: 100%;
         left: 50%;
         transform: translate(-50%, 0);
         z-index: 102 !important;
         padding: 10px;
         border-radius: 5px;
         border: 1px solid #dad3cc;
         background: #fff9f8;
         div{
            margin-bottom: 4px;
            &:last-child{
               margin-bottom: 0;
            }
         }
      }
      &-hide {
         display: none;
      }
   }
   &__menu {
      @media (max-width: 992px) {
         display: none;
         position: fixed;
         left: 0;
         right: 0;
         top: 84px;
         bottom: 0;
         width: 100%;
         height: 100%;
         z-index: 99;
         background: #fff9f8;
         overflow-y: auto;
         flex-direction: column;
         align-items: center;
         padding: 30px 0 150px;
         &::-webkit-scrollbar {
            display: none;
         }
         &.active {
            display: flex;
         }
      }
   }
   &__menu-btn {
      display: none;
      @media (max-width: 992px) {
         display: block;
      }
   }
   &__menu-list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      list-style: none;
      overflow-x: hidden;
      @media (max-width: 992px) {
         flex-direction: column;
         overflow-x: unset;
         justify-content: flex-start;
         margin-top: 30px;
         .nav__list-item {
            margin-bottom: 20px;
         }
      }
   }
   &__list-item > a {
      font-weight: 300 !important;
      color: #2e1a1e !important;
      transition: all 0.3s ease;
      @media (max-width: 992px) {
         font-size: 28px !important;
      }
      &:hover {
         transition: all 0.3s ease;
         color: #7a5da8 !important;
      }
   }
   &__menu-lang {
      display: none;
      justify-content: space-between;
      align-items: center;
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      background: #fff9f8;
      @media (max-width: 600px) {
         display: flex;
      }
   }
   &__lang-item {
      padding: 10px 21px;
      color: #8b746d !important;
      &.active {
         color: #2e1a1e !important;
         background: #fff2ee;
      }
   }
   .burger {
      @media (max-width: 992px) {
         width: 53px;
         height: 53px;
         position: relative;
         z-index: 100;
         background: #fff2ee;
         border-radius: 10px;
         margin-left: 20px;
         &::after,
         &::before,
         span {
            content: "";
            position: absolute;
            width: 21px;
            height: 2px;
            transform: translate(-50%, 0);
            border-radius: 2px;
            left: 50%;
            background-color: #7a5da8;
            transition: 0.3s ease 0s;
            transform-origin: right;
         }
         &::before {
            top: 18px;
         }
         &::after {
            bottom: 17px;
         }
         span {
            width: 14px;
            top: 50%;
            transform: translate(calc(-50% + 3px), -50%);
         }
         &.active {
            &::before,
            &::after {
               left: 45%;
               transition: 0.3s ease 0s;
            }
            &::before {
               transform: translate(-50%, -50%) rotate(-45deg);
               top: 20px;
            }
            &::after {
               transform: translate(-50%, -50%) rotate(45deg);
               bottom: 16px;
            }
            span {
               transform: translate(-50%, -50%) scaleX(0);
               transition: 0.3s ease 0s;
            }
         }
      }
      @media (max-width: 600px) {
         border-radius: 0px 0px 0px 10px;
         position: absolute;
         right: 0;
         top: 0;
      }
   }
}
.modal__wrapper {
   display: none;
   position: fixed;
   left: 0;
   top: 0;
   right: 0;
   bottom: 0;
   justify-content: center;
   align-items: center;
   background: #ffffffde;
   z-index: 100;
}
#modalNav {
   @media (max-width: 600px) {
      align-items: flex-start;
   }
   .modal {
      background: #ffffff;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07);
      border-radius: 25px;
      max-width: 695px;
      width: 100%;
      padding: 50px;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      @media (max-width: 600px) {
         max-width: 100%;
         height: 100%;
         padding: 76px 15px 0;
         &__btn {
            width: 100%;
         }
      }
      &__close {
         position: absolute;
         right: 20px;
         top: 20px;
         cursor: pointer;
      }
      &__title {
         margin-bottom: 50px;
         text-align: center;
         @media (max-width: 600px) {
            margin-bottom: 30px;
         }
      }
      &__text {
         color: #594c4f !important;
      }
      &__content {
         padding-bottom: 9px;
         display: flex;
         align-items: center;
         justify-content: flex-start;
         border-bottom: 1px solid #dad3cc;
         margin-bottom: 50px;
         width: 100%;
         @media (max-width: 600px) {
            margin-bottom: 30px;
         }
      }
      .select {
         position: relative;
         width: 100%;
         select {
            display: none;
         }
         &-selected {
            padding-left: 9px;
            &::after {
               content: url(..//img/modal-arrow-down.svg);
               position: absolute;
               right: 0;
               top: 35%;
               transform: translate(0, -50%);
            }
         }
         &-arrow:after {
            transform: translate(0, -30%) rotate(180deg);
         }
         div,
         &-selected {
            cursor: pointer;
            user-select: none;
         }
         div {
            font-size: 18px !important;
            color: #594c4f !important;
         }
         &-items {
            position: absolute;
            top: 100%;
            width: 100%;
            left: 0;
            right: 0;
            z-index: 99 !important;
            background: #ffffff;
            padding: 18px 0 10px 9px;
            div {
               margin-bottom: 5px;
               &:last-child {
                  margin-bottom: 0;
               }
            }
         }
         &-hide {
            display: none;
         }
      }
   }
}
#modalTel {
   @media (max-width: 600px) {
      align-items: flex-start;
   }
   .modal {
      background: #ffffff;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07);
      border-radius: 25px;
      max-width: 618px;
      width: 100%;
      padding: 50px 40px;
      position: relative;
      @media (max-width: 600px) {
         max-width: 100%;
         height: 100%;
         padding: 76px 15px 0;
         &__btn {
            width: 100%;
         }
      }
      &__close {
         position: absolute;
         right: 20px;
         top: 20px;
         cursor: pointer;
      }
      &__title {
         margin-bottom: 30px;
         @media (max-width: 600px) {
            text-align: center;
         }
      }
      &__form {
         display: flex;
         align-items: center;
         @media (max-width: 600px) {
            flex-direction: column;
            align-items: flex-start;
         }
      }
      &__form-input {
         font-weight: 300 !important;
         background: none;
         border: none;
         padding-bottom: 8px;
         border-bottom: 1px solid #dad3cc;
         margin-right: 30px;
         width: 100%;
         @media (max-width: 600px) {
            margin-right: 0;
            margin-bottom: 30px;
         }
      }
      &__form-btn {
         border: none;
         width: auto;
         white-space: nowrap;
         @media (max-width: 600px) {
            width: 100%;
         }
      }
   }
}

