.section {
   &__price {
      padding: 120px 0;
      background: #fcfbfa;
      &-service{
         .price{
            &__item-header{
               display: flex;
               align-items: center;
               margin-bottom: 30px;
               .price__item-title{
                  margin-bottom: 0;
               }
               .zone__header-container{
                  margin-bottom: 0;
                  @media (max-width: 600px){
                     .zone__header-item{
                        span{
                           display: none;
                        }
                        &.active span{
                           display: block;
                        }
                     }
                  }
               }
            }
            &__item-table{
               display: none;
               &.active{
                  display: table;
               }
            }
         }
      }
   }
}
.price {
   .zone__header-container {
      justify-content: center;
      margin-bottom: 55px;
   }
   &__items {
      display: none;
      &.active {
         display: block;
      }
   }
   &__item {
      margin-bottom: 50px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      &:last-child {
         margin-bottom: 0;
         .price__item-table tbody tr:nth-child(2) td {
            border-bottom: 1px solid #fff2ee;
            border-spacing: 1px !important;
            border-collapse: separate !important;
         }
         .price__item-table tbody tr:nth-child(3) td {
            padding-top: 14px;
         }
      }
   }
   &__item-img {
      background: #fff2ee;
      border-radius: 25px 25px 0px 25px;
      min-width: 330px;
      max-width: 330px;
      width: 100%;
      height: 312px;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
         width: auto;
         height: auto;
         object-fit: contain;
         object-position: center center;
      }
      @media (max-width: 1110px) {
         max-width: 250px;
         min-width: 250px;
         height: 250px;
      }
      @media (max-width: 992px) {
         display: none;
      }
   }
   &__item-content {
      width: 100%;
      margin-left: 30px;
      @media (max-width: 992px) {
         margin-left: 0;
      }
   }
   &__item-title {
      margin-bottom: 22px;
      @media (max-width: 600px) {
         margin-bottom: 29px;
      }
   }
   &__table-subtext {
      font-size: 15px !important;
      padding: 5px 0 0 30px;
   }
   &__item-table {
      width: 100%;
      background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd;
      border: 1px solid #fff2ee;
      border-radius: 20px 0px 20px 0px;
      td {
         width: 50%;
         @media (max-width: 1110px) {
            width: auto;
         }
      }
      tbody {
         padding: 10px 0;
         td {
            padding: 10px 40px;
            &:last-child {
               border-left: 1px solid #fff2ee;
            }
         }
         tr {
            border-spacing: 0px !important;
            border-collapse: collapse !important;
            &.highlight {
               background: #fff2ee;
            }
         }
         tr:first-child td {
            padding: 10px 40px;
         }
         tr:last-child td {
            padding: 10px 40px;
            width: 100% !important;
         }
         @media (max-width: 768px) {
            td {
               padding: 7.5px 15px 7.5px;
            }
            tr:first-child td {
               padding: 20px 15px 7.5px;
            }
            tr:last-child td {
               padding: 7.5px 15px 20px;
            }
         }
      }
   }
   &__table-header {
      background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd;
      border: 1px solid #fff2ee;
      border-radius: 20px 0px 0px 0px;
      padding: 18px 40px;
      font-weight: 800 !important;
      text-align: left;
      @media (max-width: 768px) {
         padding: 13px 14px;
      }
      &:last-child {
         border-radius: 0;
      }
   }
   .custom-checkbox > input {
      position: absolute;
      z-index: -1;
      opacity: 0;
   }
   .custom-checkbox > span {
      display: inline-flex;
      align-items: center;
      user-select: none;
   }
   .custom-checkbox > span::before {
      content: "";
      display: inline-block;
      width: 17px;
      height: 17px;
      background: #ffffff;
      border: 1px solid #dad3cc;
      border-radius: 3px;
      margin-right: 13px;
      @media (max-width: 768px) {
         margin-right: 5px;
         width: 100%;
         min-width: 14px;
         max-width: 14px;
         height: 14px;
      }
   }
   .custom-checkbox > input:not(:disabled):active + span::before {
      background-image: #7a5da8;
   }
   .custom-checkbox > input:checked + span::before {
      background: #7a5da8;
   }
   span.link {
      color: #2e1a1e !important;
      @media (max-width: 768px) {
         font-size: 13px !important;
      }
   }
   &__table-text {
      white-space: nowrap;
   }
   &__fixed {
      position: fixed;
      padding: 35px 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #ffffff;
      border: 1px solid #fff2ee;
      box-shadow: 0px -4px 50px rgba(0, 0, 0, 0.07);
      transform: translate(0, 100%);
      transition: all .3s ease;
      &.active{
         transform: translate(0, 0);
         transition: all .3s ease;
      }
      @media (max-width: 768px){
         padding: 10px 0;
      }
   }
   &__content {
      max-width: 964px;
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 768px){
         justify-content: space-between;
      }
   }
   &__price {
      margin-right: 50px;
      @media (max-width: 400px){
         margin-right: 0;
         &-subtitle{
            display: none;
         }
      }
   }
   &__price-title {
      font-weight: 500 !important;
      color: #2e1a1e !important;
      margin-top: 5px;
   }
   &__form{
      display: flex;
      align-items: flex-start;
   }
   &__form-input{
      border: none;
      border-bottom: 1px solid #DAD3CC;
      background: none;
      padding-bottom: 8px;
      font-weight: 300 !important;
      margin-right: 30px;
      @media (max-width: 768px){
         display: none;
      }
   }
   &__form-btn{
      border: none;
   }
}
