.section {
   &__stock {
      padding: 80px 0 120px;
      @media (max-width: 768px){
         padding: 50px 0 70px !important;
      }
   }
}

.stock {
   display: flex;
   align-items: flex-start;
   @media (max-width: 992px){
      flex-direction: column;
   }
   &__img {
      width: auto;
      border-radius: 25px 25px 0px 25px;
      overflow: hidden;
      img {
         width: 100%;
      }
      @media (max-width: 992px){
         width: 100%;
         margin-bottom: 24px;
      }
   }
   &__content {
      margin-left: 118px;
      max-width: 600px;
      width: 100%;
      @media (max-width: 1280px){
         margin-left: 30px;
      }
      @media (max-width: 1110px){
         max-width: 500px;
      }
      @media (max-width: 992px){
         max-width: 100%;
         margin-left: 0;
      }
   }
   &__title {
      margin-bottom: 15px !important;
   }
   &__text {
      margin-bottom: 30px;
   }
   &__subtext {
      color: #8B746D !important;
      font-weight: 600 !important;
   }
}
