.section {
   &__gallery {
      padding: 120px 0;
   }
}
.gallery {
   display: flex;
   align-items: center;
   justify-content: space-between;
   @media (max-width: 992px) {
      flex-direction: column;
   }
   &__img {
      width: 100%;
      position: relative;
      z-index: 0;
      @media (max-width: 992px) {
         margin-bottom: 30px;
      }
      &::after {
         content: url(..//img/lazer-decor.svg);
         position: absolute;
         right: -10%;
         top: 10%;
         z-index: -1;
         transform: rotate(180deg);
         @media (max-width: 1110px){
            right: -20%;
            top: -5%;
         }
         @media (max-width: 992px){
            display: none;
         }
      }
      img {
         width: 100%;
         height: auto;
      }
   }
   &__content {
      margin-left: 120px;
      max-width: 430px;
      width: 100%;
      @media (max-width: 1110px) {
         margin-left: 30px;
      }
      @media (max-width: 992px) {
         margin-left: 0;
         max-width: 100%;
      }
   }
   &__title {
      margin-bottom: 15px !important;
   }
   &__text {
      margin-bottom: 70px;
      @media (max-width: 992px) {
         margin-bottom: 30px;
      }
   }
}
