.section {
   &__comments {
      padding: 120px 0;
      &-reviews {
         padding: 80px 0 120px !important;
         @media (max-width: 768px){
            padding: 50px 0 70px !important;
         }
         .comments {
            position: relative;
            max-width: 100%;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            &__content{
               margin-top: 50px;
            }
            &__header {
               display: block;
            }
            &__title{
               text-align: left;
               margin-top: 16px;
            }
            &__container {
               max-width: 930px;
               width: 100%;
               @media (max-width: 1280px){
                  max-width: 800px;
               }  
            }
            &__menu {
               display: flex;
               align-items: center;
               justify-content: space-between;
               width: 100%;
               margin-top: 55px;
               @media (max-width: 1110px){
                  margin-top: 120px;
               }
               @media (max-width: 768px){
                  flex-wrap: wrap;
               }
            }
            &__list {
               display: flex;
               align-items: center;
               width: 100%;
               @media (max-width: 768px){
                  overflow-x: auto;
                  flex: 0 1 100%;
                  margin-bottom: 27px;
               }
               &::-webkit-scrollbar {
                  height: 5px;
               }
               &::-webkit-scrollbar-thumb {
                  background: #dad3cc;
                  border-radius: 5px;
               }
            }
            &__list-item {
               margin-right: 30px;
               margin-left: 17px;
               position: relative;
               &::after {
                  content: "";
                  width: 13px;
                  height: 13px;
                  background: #ffffff;
                  border: 1px solid #dad3cc;
                  border-radius: 50%;
                  position: absolute;
                  top: 3px;
                  left: -17px;
               }
               &.active {
                  &::after {
                     background: #7a5da8;
                  }
               }
               &:last-child {
                  margin-right: 0;
               }
               @media (max-width: 768px){
                  white-space: nowrap;
                  &::after{
                     top: 1px;
                  }
               }
            }
            .specialist__location {
               min-width: unset !important;
               max-width: 406px !important;
               @media (max-width: 1280px){
                  max-width: 220px !important;
               }
               @media (max-width: 992px){
                  .select{
                     min-width: 120px !important;
                  }
               }
               @media (max-width: 768px){
                  max-width: 100% !important;
               }
            }
            &__rate {
               &-rate {
                  display: flex;
                  align-items: center;
                  margin-bottom: 5px;
                  justify-content: space-between;
               }
               &-text{
                  margin-bottom: 56px;
                  color: #8B746D !important;
                  font-weight: 300 !important;
               }
               @media (max-width: 1110px){
                  position: absolute;
                  right: 0;
                  top: 10px;
                  width: 100%;
                  justify-content: flex-end;
                  &-rate{
                     justify-content: flex-end;
                  }
                  &-title{
                     margin-left: 15px;
                  }
                  &-text{
                     text-align: end;
                  }
                  &-btn{
                     position: absolute;
                     left: 50%;
                     transform: translate(-50%, 0);
                     top: 60px;
                     display: flex;
                     justify-content: center;
                  }
               }
               @media (max-width: 480px){
                  &-btn{
                    width: 100%;
                  }
                  &-text{
                     font-size: 16px !important;
                  }
               }
            }
            &__items{
               display: none;
               &.active{
                  display: block;
               }
               &#commentTab3{
                  grid-template-columns: repeat(2, 1fr);
                  width: 100%;
                  gap: 30px;
                  @media (max-width: 768px){
                     gap: 15px;
                  }
                  @media (max-width: 600px){
                     grid-template-columns: repeat(1, 1fr);
                  }
                  .comments__item{
                     margin: 0;
                     padding: 0;
                     width: 100%;
                     img{
                        width: 100%;
                     }
                  }
                  .comments__content{
                     grid-column: 1/ 3;
                  }
                  &.active{
                     display: grid;
                  }
               }
            }
         }
      }
   }
}
.comments {
   max-width: 930px;
   width: 100%;
   margin: 0 auto;
   &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 50px;
      @media (max-width: 600px) {
         flex-direction: column;
         margin-bottom: 30px;
      }
   }
   &__title {
      margin-bottom: 0 !important;
      @media (max-width: 600px) {
         text-align: center;
         margin-bottom: 30px !important;
      }
   }
   &__items {
      margin-bottom: 35px;
      @media (max-width: 600px) {
         margin-bottom: 30px;
      }
   }
   &__item {
      margin-right: auto;
      width: 100%;
      background: #fcfbfa;
      border-radius: 30px 30px 30px 0px;
      padding: 40px 50px;
      margin-bottom: 10px;
      @media (max-width: 768px) {
         padding: 20px 30px;
      }
      @media (max-width: 600px) {
         padding: 15px;
         border-radius: 15px 15px 15px 0px;
      }
      &.admin {
         width: 90%;
         margin-left: auto;
         margin-right: 0;
         margin-bottom: 20px;
      }
   }
   &__item-info {
      display: flex;
      align-items: center;
      margin-bottom: 5px;
   }
   &__item-title {
      font-family: "Gilroy ExtraBold" !important;
      margin-right: 15px;
      padding-right: 15px;
      border-right: 1px solid #dad3cc;
   }
   &__item-time {
      font-weight: 300 !important;
      color: #8b746d !important;
   }
   &__item-rate {
      margin-bottom: 20px;
      @media (max-width: 600px) {
         margin-bottom: 15px;
      }
   }
   &__item-text {
      font-weight: 300 !important;
      margin-bottom: 20px;
      @media (max-width: 600px) {
         margin-bottom: 15px;
      }
   }
   &__content {
      display: flex;
      justify-content: center;
   }
   &__btn{
      cursor: pointer;
   }
}
#modalComment {
   .modal {
      background: #ffffff;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.07);
      border-radius: 25px;
      max-width: 695px;
      width: 100%;
      padding: 50px;
      position: relative;
      @media (max-width: 600px) {
         max-width: 100%;
         height: 100%;
         padding: 76px 15px 0;
         &__btn {
            width: 100%;
         }
      }
      &__close {
         position: absolute;
         right: 20px;
         top: 20px;
         cursor: pointer;
      }
      &__subtitle {
         margin-bottom: 30px;
      }
      &__rate-stars {
         height: 100%;
         display: flex;
         align-items: center;
      }
      &__text {
         color: #594c4f !important;
      }
      &__rate {
         display: flex;
         align-items: center;
         margin-bottom: 30px;
      }
      &__text {
         margin-right: 10px;
      }
      &__form {
         display: flex;
         flex-direction: column;
         width: 100%;
      }
      &__form-input {
         color: #8b746d !important;
         font-weight: 300 !important;
         padding-bottom: 8px;
         border: none;
         background: none;
         border-bottom: 1px solid #dad3cc;
         margin-bottom: 30px;
      }
      &__form-textarea {
         resize: none;
         height: 167px;
      }
      &__form-btn {
         border: none;
         margin-left: auto;
         margin-right: 0;
         cursor: pointer;
         @media (max-width: 600px) {
            margin-right: auto;
            margin-left: 0;
         }
      }
   }
}
