.section {
   &__specialist {
      padding: 0 0 120px;
      &-specialist{
         padding: 80px 0 130px;
         @media (max-width: 992px){
            padding: 30px 0 70px !important;
         }
      }
   }
}
.specialist {
   &__swiper {
      margin-bottom: 50px;
      .slide {
         background: #fff9f8;
         border-radius: 25px 25px 25px 0px;
         &__img {
            width: 100%;
            height: auto;
            border-radius: 25px 25px 0px 0px;
            overflow: hidden;
            img {
               width: 100%;
               height: 100%;
            }
         }
         &__content {
            padding: 15px 20px 20px;
         }
         &__title {
            margin-bottom: 15px;
         }
         &__location {
            display: flex;
            align-items: flex-start;
            margin-bottom: 15px;
         }
         &__location-content {
            margin-left: 5px;
            margin-top: -5px;
         }
         &__text {
            font-weight: 300 !important;
            color: #8B746D !important;
            span {
               font-weight: 500 !important;
            }
         }
         &__rate {
            display: flex;
            align-items: center;
            margin-bottom: 15px;
         }
         &__rate-stars {
            margin-right: 22px;
         }
         &__subtext {
            font-weight: 300 !important;
         }
         &__btn {
            width: 100%;
            display: inline-flex;
            justify-content: flex-end;
         }
      }
   }
   &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__items {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 30px;
      @media (max-width: 1280px) {
         grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 992px) {
         grid-template-columns: repeat(2, 1fr);
         gap: 20px;
      }
      @media (max-width: 600px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }
   &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 50px;
      .specialist__title {
         margin-bottom: 0;
      }
      @media (max-width: 768px){
         flex-direction: column;
         align-items: flex-start;
         .specialist__title{
            margin-bottom: 30px;
         }
      }
   }
   &__location {
      display: flex;
      align-items: center;
      padding-bottom: 5px;
      border-bottom: 1px solid #dad3cc;
      @media (max-width: 768px){
         justify-content: flex-start;
         width: 100%;
      }
      img {
         margin-right: 5px;
      }
   }
   &__location-text {
      font-weight: 300 !important;
   }
}
.specialist__select.select {
   position: relative;
   min-width: 200px;
   width: 100%;
   margin-left: 10px;
   @media (max-width: 768px){
      min-width: unset;
   }
   select {
      display: none;
   }
   .select-selected {
      &::after {
         content: url(..//img/modal-arrow-down.svg);
         position: absolute;
         right: 0;
         top: 35%;
         transform: translate(0, -50%);
      }
   }
   .select-arrow:after {
      transform: translate(0, -30%) rotate(180deg);
   }
   div {
      font-weight: 300 !important;
   }
   div,
   .select-selected {
      cursor: pointer;
      user-select: none;
   }
   .select-items {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      z-index: 99 !important;
      padding: 10px 0;
      background: #ffffff;
      div {
         margin-bottom: 5px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   .select-hide {
      display: none;
   }
}
