.section {
   &__zone {
      padding: 120px 0;
      &-gallery {
         padding: 80px 0 120px !important;
         .zone {
            &__header-item {
               @media (max-width: 600px) {
                  span {
                     display: none;
                  }
                  &.active span {
                     display: block;
                  }
               }
            }
            &__tab-header {
               margin-bottom: 50px;
            }
            &__tab-content {
               justify-content: center;
            }
            &__tab-swiper {
               display: grid;
               grid-template-columns: repeat(3, 1fr);
               gap: 30px;
               @media (max-width: 992px) {
                  gap: 15px;
                  grid-template-columns: repeat(2, 1fr);
               }
               @media (max-width: 600px) {
                  width: 100%;
                  grid-template-columns: repeat(1, 1fr);
               }
               .slide {
                  width: 100%;
                  padding: 0;
                  @media (max-width: 600px) {
                     min-width: 100%;
                  }
                  &__img {
                     overflow: hidden;
                     display: flex;
                     align-items: flex-start;
                     height: auto;
                     width: 100%;
                     margin-bottom: 15px;
                     border-bottom: none;
                     border-radius: 20px 20px 0px 0px;
                     &-after,
                     &-before {
                        width: 50%;
                        height: 100%;
                     }
                     img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                     }
                  }
                  &__content {
                     padding: 0 20px 15px;
                  }
                  &__btn {
                     justify-content: flex-end;
                  }
               }
            }
         }
      }
      &-price {
         .zone {
            &__title {
               margin-bottom: 53px !important;
               @media (max-width: 600px){
                  margin-bottom: 30px !important;
               }
            }
            &__header {
               justify-content: space-between;
               @media (max-width: 600px){
                  margin-bottom: 30px;
               }
            }
         }
      }
      &-service{
         .zone{
            &__title{
               margin-bottom: 0 !important;
            }
         }
      }
   }
}
.zone {
   &__header {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 63px;
   }
   &__title {
      margin-bottom: 0 !important;
   }
   &__header-container {
      display: flex;
      align-items: center;
      margin: 0 auto;
      list-style: none;
      @media (max-width: 600px) {
         margin-left: auto;
         margin-right: 0;
      }
   }
   &__header-item {
      opacity: 0.7;
      transition: all 0.3s ease;
      margin-right: 35px;
      display: flex;
      align-items: center;
      cursor: pointer;
      font-weight: 600 !important;
      @media (max-width: 600px) {
         margin-right: 17px;
      }
      img {
         margin-right: 5px;
      }
      &.active {
         opacity: 1;
      }
      &.female {
         color: #7a5da8 !important;
      }
      &.male {
         margin-right: 0;
         color: rgba(106, 143, 166) !important;
      }
      @media (max-width: 600px) {
         span {
            display: none;
         }
         &.active span {
            display: block;
         }
      }
   }
   &__tab {
      display: none;
      &.active {
         display: block;
      }
   }
   &__tab-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 30px;
      overflow-x: auto;
      padding-bottom: 19px;
      border-bottom: 1px solid #dad3cc;
      &::-webkit-scrollbar {
         height: 5px;
      }
      &::-webkit-scrollbar-thumb {
         background: #dad3cc;
         border-radius: 5px;
      }
   }
   &__tab-content {
      margin-right: 26px;
      &:last-child {
         margin-right: 0;
      }
   }
   &__tab-content a{
      color: #594c4f !important;
      white-space: nowrap;
      &.active {
         color: #2e1a1e !important;
         font-weight: 600 !important;
      }
   }
   &__tab-item {
      display: none;
      &.active {
         display: block !important;
      }
   }
   &__tab-swiper {
      margin-bottom: 50px;
      .slide {
         background: #fff9f8;
         border-radius: 25px 25px 25px 0px;
         transition: all 0.3s ease;
         padding: 20px;
         @media (max-width: 600px) {
            max-width: 263px;
         }
         &:hover {
            background: #fff2ee;
            transition: all 0.3s ease;
         }
         &__img {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 300px;
            width: 100%;
            margin-bottom: 23px;
            border-bottom: 1px solid #dad3cc;
            @media (max-width: 600px) {
               height: auto;
               margin-bottom: 15px;
            }
            img {
               width: auto;
               height: auto;
               object-fit: contain;
               object-position: center center;
            }
         }
         &__header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 40px;
            @media (max-width: 600px) {
               margin-bottom: 11px;
            }
         }
         &__time {
            display: flex;
            align-items: center;
         }
         &__subtext {
            color: #594c4f;
         }
         &__price {
            display: flex;
            align-items: center;
            justify-content: space-between;
         }
      }
   }
   &__tab-content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 480px) {
         justify-content: center;
      }
   }
}
