.section {
   &__lazer {
      padding: 80px 0 120px;
      @media (max-width: 768px) {
         padding: 50px 0 70px !important;
      }
   }
}
.lazer {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 992px) {
      flex-direction: column;
   }
   &.reverse {
      flex-direction: row-reverse;
      .lazer__img::after {
         right: -60%;
         left: auto;
         transform: translate(-50%, -50%) rotate(270deg);
         @media (max-width: 1434px), (max-width: 1280px ) {
            right: -50%;
         }
         @media (max-width: 1110px) {
            right: -70%;
         }
      }
      .lazer__content {
         margin-left: 124px;
         @media (max-width: 1434px) {
            margin-left: 30px;
         }
         @media (max-width: 992px){
            margin-left: 0;
         }
      }
   }
   &__content {
      max-width: 602px;
      width: 100%;
      margin-right: 124px;
      @media (max-width: 1434px){
         margin-right: 30px;
      }
      @media (max-width: 1280px) {
         max-width: 500px;
      }
      @media (max-width: 992px) {
         max-width: 100%;
         margin-right: 0;
         margin-left: 0;
      }
   }
   &__title {
      margin-bottom: 20px !important;
   }
   &__text {
      font-size: 15px !important;
      margin-bottom: 15px;
   }
   &__btn {
      margin-top: 45px;
   }
   &__img {
      width: auto;
      z-index: 0;
      position: relative;
      @media (max-width: 992px) {
         display: none;
      }
      &::after {
         content: url(..//img/lazer-decor.svg);
         position: absolute;
         left: -50%;
         bottom: -60%;
         transform: translate(50%, -50%);
         z-index: -1;
         @media (max-width: 1434px) {
            bottom: -70%;
            left: -60%;
         }
         @media (max-width: 1280px) {
            left: -60%;
         }
         @media (max-width: 1110px) {
            bottom: -90%;
            left: -70%;
         }
      }
      img {
         width: 100%;
      }
      &-mob {
         display: none;
         @media (max-width: 992px) {
            display: block;
            width: 100%;
            margin-bottom: 30px;
         }
         img {
            width: 100%;
            height: auto;
         }
      }
   }
}
