.section {
   &__info {
      padding: 30px 0 120px;
   }
}
.info {
   display: flex;
   align-items: flex-start;
   justify-content: space-between;
   @media (max-width: 992px) {
      flex-direction: column;
   }
   &__img {
      width: auto;
      @media (max-width: 992px) {
         width: 100%;
         margin-bottom: 52px;
      }
      img {
         width: 100%;
      }
   }
   &__content {
      max-width: 840px;
      width: 100%;
      margin-left: 67px;
      @media (max-width: 1434px) {
         max-width: 700px;
      }
      @media (max-width: 1280px) {
         max-width: 600px;
      }
      @media (max-width: 1110px) {
         margin-left: 30px;
         max-width: 500px;
      }
      @media (max-width: 992px) {
         margin-left: 0;
         max-width: 100%;
      }
   }
   &__title {
      margin-bottom: 15px !important;
   }
   &__content-block {
      display: flex;
      align-items: center;
      margin-bottom: 33px;
      @media (max-width: 600px) {
         flex-direction: column;
         align-items: flex-start;
         margin-bottom: 25px;
      }
   }
   &__subtitle {
      color: #8b746d !important;
      padding-right: 25px;
      margin-right: 25px;
      border-right: 1px solid #dad3cc;
      &:last-child {
         padding-right: 0;
         margin-right: 0;
         border: none;
      }
      @media (max-width: 600px) {
         padding-right: 0;
         margin-right: 0;
         border: none;
         margin-bottom: 10px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   &__content-items {
      margin-bottom: 50px;
      @media (max-width: 600px) {
         margin-bottom: 30px;
      }
   }
   &__content-item {
      margin-bottom: 27px;
      &:last-child {
         margin-bottom: 0;
      }
      @media (max-width: 600px) {
         margin-bottom: 20px;
      }
   }
   &__item-title {
      font-family: "Gilroy ExtraBold" !important;
      color: #2e1a1e !important;
      margin-bottom: 17px;
      @media (max-width: 600px) {
         margin-bottom: 10px;
      }
   }
   &__item-list {
      margin-left: 10px;
      li {
         position: relative;
         &::after {
            content: "";
            position: absolute;
            left: -10px;
            width: 5px;
            height: 5px;
            top: 9px;
            border-radius: 50%;
            background: #7a5da8;
         }
      }
   }
   &__list-text {
      font-weight: 300 !important;
   }
}
.breadcrumps {
   display: flex;
   align-items: center;
   margin-bottom: 84px;
   @media (max-width: 992px){
      margin-bottom: 34px;
   }
   @media (max-width: 768px){
      display: none;
   }
   li {
      padding-right: 5px;
      margin-right: 5px;
      border-right: 1px solid #8b746d;
      &:last-child {
         padding-right: 0;
         margin-right: 0;
         border: none;
      }
   }
   &__link {
      color: #8b746d !important;
      font-weight: 300 !important;
   }
}
