.section {
   &__safe {
      padding: 120px 0;
   }
}
.safe {
   &__title {
      margin-bottom: 0 !important;
      @media (max-width: 768px){
         margin-right: 20px;
      }
   }
   &__header{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      .zone__header-container{
         display: none;
         span{
            display: none !important;
         }
      }
      @media (max-width: 768px){
         margin-bottom: 30px;
         .zone__header-container{
            display: flex;
         }
      }
   }
   &__items {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 768px){
         flex-direction: column;
      }
   }
   &__item {
      position: relative;
      background: #fef7f7;
      border-radius: 50px 50px 0px 50px;
      margin-right: 64px;
      width: 100%;
      padding: 34px 0;
      display: flex;
      justify-content: center;
      &.female {
         .safe__item-container {
            &:nth-child(2) {
               top: 218px;
               right: 283px;
            }
            &:nth-child(3) {
               top: 362px;
               right: 336px;
            }
            &:nth-child(4) {
               top: 438px;
               right: 297px;
            }
         }
      }
      &.male {
         .safe__item-container {
            &:nth-child(2) {
               top: 133px;
               right: 294px;
            }
            &:nth-child(3) {
               top: 238px;
               right: 313px;
            }
            &:nth-child(4) {
               top: 252px;
               right: 253px;
            }
         }
      }
      @media (max-width: 1434px) {
         &.female {
            .safe__item-container {
               &:nth-child(2) {
                  top: 228px;
                  right: 233px;
               }
               &:nth-child(3) {
                  top: 372px;
                  right: 286px;
               }
               &:nth-child(4) {
                  top: 448px;
                  right: 237px;
               }
            }
         }
         &.male {
            .safe__item-container {
               &:nth-child(2) {
                  right: 244px;
               }
               &:nth-child(3) {
                  top: 238px;
                  right: 263px;
               }
               &:nth-child(4) {
                  top: 262px;
                  right: 203px;
               }
            }
         }
      }
      @media (max-width: 1280px) {
         &.female {
            .safe__item-container {
               &:nth-child(2) {
                  top: 238px;
                  right: 203px;
               }
               &:nth-child(3) {
                  right: 260px;
               }
               &:nth-child(4) {
                  right: 217px;
               }
            }
         }
         &.male {
            .safe__item-container {
               &:nth-child(2) {
                  right: 215px;
               }
               &:nth-child(3) {
                  right: 243px;
               }
               &:nth-child(4) {
                  right: 183px;
               }
            }
         }
      }
      @media (max-width: 1110px){
         margin-right: 34px;
         &.female {
            .safe__item-container {
               &:nth-child(2) {
                  right: 173px;
               }
               &:nth-child(3) {
                  top: 375px;
                  right: 232px;
               }
               &:nth-child(4) {
                  right: 187px;
               }
            }
         }
         &.male {
            .safe__item-container {
               &:nth-child(2) {
                  right: 185px;
               }
               &:nth-child(3) {
                  right: 210px;
               }
               &:nth-child(4) {
                  right: 153px;
               }
            }
         }
      }
      @media (max-width: 992px){
         margin-right: 24px;
         &.female {
            .safe__item-container {
               &:nth-child(2) {
                  right: 133px;
               }
               &:nth-child(3) {
                  right: 182px;
               }
               &:nth-child(4) {
                  right: 137px;
               }
            }
         }
         &.male {
            .safe__item-container {
               &:nth-child(2) {
                  right: 135px;
               }
               &:nth-child(3) {
                  right: 160px;
               }
               &:nth-child(4) {
                  right: 103px;
               }
            }
         }
      }
      @media (max-width: 768px){
         margin-right: 0;
         display: none;
         &.active{
            display: flex;
         }
         &.female {
            .safe__item-container {
               &:nth-child(2) {
                  right: calc(75px + (300 - 75) * ((100vw - 300px) / (768 - 300)));;
               }
               &:nth-child(3) {
                  right: calc(130px + (350 - 130) * ((100vw - 300px) / (768 - 300)));
               }
               &:nth-child(4) {
                  right: calc(75px + (300 - 75) * ((100vw - 300px) / (768 - 300)));
               }
            }
         }
         &.male {
            .safe__item-container {
               &:nth-child(2) {
                  right: calc(75px + (300 - 75) * ((100vw - 300px) / (768 - 300)));
               }
               &:nth-child(3) {
                  right: calc(100px + (330 - 100) * ((100vw - 300px) / (768 - 300)));
               }
               &:nth-child(4) {
                  right: calc(45px + (270 - 45) * ((100vw - 300px) / (768 - 300)));
               }
            }
         }
      }
      &:last-child {
         margin-right: 0;
         background: #f0f9fe;
         border-radius: 50px 50px 50px 0px;
      }
   }
   &__item-bg {
      width: auto;
      height: 100%;
   }
   &__item-container {
      position: absolute;
   }
   &__item-icon {
      width: 20px;
      height: 20px;
      background: #7a5da8;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      &::after {
         content: "";
         position: absolute;
         left: -50%;
         top: -50%;
         width: 40px;
         height: 40px;
         border: 1px solid rgba(122, 93, 168, 0.34);
         background: none;
         border-radius: 50%;
      }
      &:hover + .safe__item-content {
         opacity: 1;
         visibility: visible;
         transition: all 0.3s ease;
      }
   }
   &__item-content {
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      max-width: 220px;
      min-width: 220px;
      width: 100%;
      position: absolute;
      bottom: 10px;
      left: 10px;
      background: #ffffff;
      border-radius: 10px 10px 10px 0px;
      padding: 20px;
      @media (max-width: 768px){
         left: auto;
         right: calc(-100% + 30px);
      }
      &:hover{
         opacity: 1;
         visibility: visible;
         transition: all 0.3s ease;
      }
   }
   &__item-title {
      margin-bottom: 16px;
   }
   &__item-block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 18px;
   }
   &__item-time {
      display: flex;
      align-items: center;
   }
   &__item-subtext {
      color: #8b746d;
   }
   &__item-btn {
      .btn-arrow-text {
         opacity: 1;
         visibility: visible;
      }
   }
}
