.section {
   &__award {
      padding: 120px 0;
   }
}
.award {
   display: flex;
   align-items: center;
   justify-content: space-between;
   @media (max-width: 992px){
      flex-direction: column;
   }
   @media (max-width: 768px){
      .award__swiper .main-swiper-control{
         bottom: 0;
         top: auto;
      }
   }
   &__content {
      margin-right: 124px;
      max-width: 602px;
      @media (max-width: 1280px){
         margin-right: 30px;
         max-width: 500px;
      }
      @media (max-width: 992px){
         max-width: 100%;
         margin-right: 0;
         margin-bottom: 50px;
      }
   }
   &__title {
      margin-bottom: 26px !important;
   }
   .main-swiper-control{
      background: #FFFFFF !important;
   }
   &__text {
      margin-bottom: 14px;
   }
}
