.section {
   &__discount {
      padding: 117px 0;
      background: #FFF9F8;
      position: relative;
      overflow: hidden;
      z-index: 0;
      &::after{
         content: url(..//img/discount-decor1.svg);
         position: absolute;
         left: 0;
         top: 50%;
         transform: translate(0, -60%);
         z-index: -1;
      }
      &::before{
         content: url(..//img/discount-decor2.svg);
         position: absolute;
         right: 0;
         top: 0;
         z-index: -1;
      }
      @media (max-width: 1280px){
         &::after{
            left: -10%;
         }
         &::before{
            top: -50%;
            right: -10%;
         }
      }
      @media (max-width: 992px){
         &::after{
            left: -20%;
         }
      }
      @media (max-width: 768px){
         &::after{
            left: -40%;
         }
         &::before{
            right: -30%;
         }
      }
      @media (max-width: 480px){
         &::after{
            left: -60%;
         }
         &::before{
            right: -50%;
            top: -40%;
         }
      }
      @media (max-width: 400px){
         &::after{
            left: -70%;
         }
         &::before{
            right: -60%;
            top: -60%;
         }
      }
   }
}
.discount {
   &__title {
      font-family: "Gilroy ExtraBold";
      font-size: 48px;
      line-height: 1.67;
      color: #2e1a1e;
      margin-bottom: 50px;
      text-align: center;
      @media (max-width: 992px){
         font-size: 36px;
         margin-bottom: 30px;
      }
      @media (max-width: 480px){
         font-size: 30px;
         line-height: 1.2;
      }
   }
   &__form {
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 768px){
         flex-direction: column;
         align-items: flex-start;
      }
   }
   &__form-input {
      font-weight: 300 !important;
      border: none;
      border-bottom: 1px solid #DAD3CC;
      background: none;
      padding: 8px 0;
      margin-right: 30px;
      @media (max-width: 768px){
         margin-right: 0;
         margin-bottom: 15px;
         padding-bottom: 32px;
         width: 100%;
      }
   }
   &__form-btn {
      border: none;
      cursor: pointer;
      @media (max-width: 768px){
         width: 100%;
      }
   }
}
