.section {
   &__contact {
      position: relative;
      &-specialist {
         background: #fcfbfa;
         .contact {
            justify-content: flex-start;
            @media (max-width: 992px) {
               flex-direction: column-reverse;
            }
            &__map {
               right: 0;
               left: auto;
               max-width: 50%;
               @media (max-width: 1434px){
                  max-width: 70%;
               }
               @media (max-width: 1110px) {
                  max-width: 60%;
               }
               @media (max-width: 992px) {
                  max-width: 100%;
               }
            }
            &__content {
               padding: 120px 0 223px;
               @media (max-width: 992px) {
                  padding: 50px 0;
               }
            }
            &__item {
               margin-bottom: 50px;
               background: none !important;
            }
            &__icons {
               margin-top: 20px;
            }
         }
      }
      &-contact {
         @media (max-width: 768px){
            padding: 50px 0 0 !important;
         }
         .contact {
            height: 847px;
            align-items: center;
            @media (max-width: 992px){
               flex-direction: column-reverse;
               height: auto;
               &__map{
                  margin-bottom: 20px;
               }
            }
            &__accordion {
               display: block ;
               order: unset;
               padding-bottom: 0;
            }
            .accordion__item {
               border: 1px solid #fff2ee;
               border-radius: 10px 10px 0px 0px;
            }
            &__items {
               height: 527px;
            }
         }
      }
   }
}
.contact {
   display: flex;
   justify-content: flex-end;
   @media (max-width: 992px) {
      flex-direction: column;
      justify-content: center;
   }
   &__map {
      position: absolute;
      left: 0;
      top: 0;
      max-width: 55%;
      width: 100%;
      height: 100%;
      iframe {
         width: 100%;
         height: 100%;
         border: none;
      }
      @media (max-width: 992px) {
         position: relative;
         max-width: 100%;
         order: 2;
         height: 451px;
         top: unset;
      }
   }
   &__content {
      max-width: 494px;
      width: 100%;
      @media (max-width: 1280px) {
         max-width: 450px;
      }
      @media (max-width: 1110px) {
         max-width: 400px;
      }
      @media (max-width: 992px) {
         max-width: 100%;
         order: 1;
      }
   }
   &__title {
      margin-bottom: 20px !important;
   }
   &__info {
      display: flex;
      align-items: center;
      color: #594c4f;
      margin-bottom: 10px;
      img {
         margin-right: 8px;
      }
   }
   &__content-block {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
   }
   &__icons {
      display: flex;
      align-items: center;
      li {
         margin-right: 20px;
         &:last-child {
            margin-right: 0;
         }
      }
   }
   &__icon {
   }
   &__items {
      height: 629px;
      overflow-y: auto;
      &::-webkit-scrollbar {
         width: 3px;
      }
      &::-webkit-scrollbar-thumb {
         background: #7a5da8;
      }
      &::-webkit-scrollbar-track {
         background: #ffffff;
      }
      @media (max-width: 992px) {
         display: none;
      }
   }
   &__item {
      padding: 13px 16px 11px 41px;
      background: #ffffff;
      transition: all 0.3s ease;
      &.active,
      &:hover {
         background: #fff9f8;
         transition: all 0.3s ease;
      }
   }
   &__item-location {
      margin-bottom: 5px;
      position: relative;
      &::after {
         content: url(..//img/location.svg);
         position: absolute;
         left: -22px;
         top: 2px;
      }
   }
   &__item-text {
      margin-bottom: 5px;
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__item-metro {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      img {
         margin-right: 10px;
      }
   }
   &__metro-text {
      color: #8b746d;
   }
   &__accordion {
      order: 3;
      padding-bottom: 70px;
      display: none;
      @media (max-width: 992px) {
         display: block;
      }
   }
   .accordion {
      &__item {
         &.show {
            .accordion__header {
               transition: all 0.3s ease;
               padding-bottom: 24px;
               &-icon::before {
                  transition: all 0.3s ease;
                  transform: translate(-50%, -50%) rotate(-90deg);
               }
            }
            .accordion__content {
               height: auto;
               opacity: 1;
               visibility: visible;
               transition: all 0.3s ease;
            }
         }
      }
      &__header {
         cursor: pointer;
         padding: 20px 15px;
         width: 100%;
         display: flex;
         align-items: center;
         justify-content: space-between;
      }
      &__header-icon {
         position: relative;
         min-width: 23px;
         max-width: 23px;
         width: 100%;
         height: 23px;
         @media (max-width: 600px) {
            max-width: 17px;
            min-width: 17px;
            height: 17px;
            margin-left: 30px;
         }
         &::after,
         &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: #7a5da8;
            border-radius: 3px;
            transition: all 0.3s ease;
         }
         &::after {
            width: 100%;
            height: 3px;
         }
         &::before {
            width: 3px;
            height: 100%;
         }
      }
      &__content {
         height: 0;
         opacity: 0;
         transition: all 0.3s ease;
         visibility: hidden;
         .contact__items {
            display: block;
         }
      }
   }
}
