.section {
   &__hero {
      padding: 30px 0 77px !important;
      position: relative;
      overflow: hidden;
      z-index: 0;
      &::after {
         content: url(..//img/hero-decor.webp);
         position: absolute;
         top: 0;
         left: 0;
         z-index: -1;
        // transform: translate(50%, -50%);
      }
   }
}
.hero {
   display: flex;
   justify-content: space-between;
   align-items: center;
   @media (max-width: 992px) {
      flex-direction: column;
      justify-content: center;
      text-align: center;
   }
   &__content {
      margin-right: 32px;
      @media (max-width: 992px) {
         margin-right: 0;
         margin-bottom: 50px;
      }
   }
   &__subtitle {
      font-family: "Inter";
      font-weight: 300;
      font-size: 30px;
      line-height: 1.2;
      color: #594c4f;
      margin-bottom: 20px;
      @media (max-width: 1434px) {
         font-size: 26px;
         margin-bottom: 15px;
      }
      @media (max-width: 992px) {
         margin-bottom: 6px;
      }
      @media (max-width: 600px) {
         font-size: 18px;
      }
   }
   &__title {
      font-family: "Gilroy ExtraBold";
      font-size: 58px;
      line-height: 1.38;
      color: #2e1a1e;
      margin-bottom: 85px;
      @media (max-width: 1434px) {
         font-size: 42px;
      }
      @media (max-width: 992px) {
         margin-bottom: 30px;
      }
      @media (max-width: 600px) {
         font-size: 36px;
      }
   }
   &__swiper-container{
      position: relative;
      z-index: 0;
      @media (max-width: 768px){
         max-width: 100%;
      }
      &::after {
         content: url(..//img/hero-swiper-decor.svg);
         position: absolute;
         bottom: -16px;
         right: -45%;
         z-index: -1;
         transform: translate(-50%, 0);
         @media (max-width: 992px){
            display: none;
         }
      }
   }
   
}
