.section {
   &__questions {
      padding: 164px 0 120px;
   }
}
.questions {
   max-width: 930px;
   width: 100%;
   margin: 0 auto;
   &__title {
      margin-bottom: 70px !important;
   }
   &__accordion {
   }
   .accordion {
      &__item {
         background: #fff9f8;
         border-radius: 20px 20px 0px 20px;
         padding: 30px;
         margin-bottom: 20px;
         transition: all .3s ease;
         @media (max-width: 600px){
            padding: 12px 15px;
            border-radius: 10px 10px 0px 10px;
         }
         &.show {
            background: #FFF2EE;
            transition: all .3s ease;
            .accordion__header{
               transition: all .3s ease;
               padding-bottom: 24px;
               border-bottom: 0.5px solid #dad3cc;
               &-icon::before {
                  transition: all .3s ease;
                  transform: translate(-50%, -50%) rotate(-90deg);
               }
            }
            .accordion__content{
               padding-top: 24px;
               height: auto;
               opacity: 1;
               visibility: visible;
               transition: all .3s ease;
            }
            @media (max-width: 600px){
               .accordion__header{
                  padding-bottom: 10px;
               }
               .accordion__content{
                  padding-top: 10px;
               }
            }
         }
      }
      &__header {
         display: flex;
         justify-content: space-between;
         align-items: center;
         cursor: pointer;
         transition: all .3s ease;
      }
      &__header-title {
         font-weight: 500 !important;
      }
      &__header-icon {
         position: relative;
         min-width: 23px;
         max-width: 23px;
         width: 100%;
         height: 23px;
         @media (max-width: 600px){
            max-width: 17px;
            min-width: 17px;
            height: 17px;
            margin-left: 30px;
         }
         &::after,
         &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: #7a5da8;
            border-radius: 3px;
            transition: all .3s ease;
         }
         &::after {
            width: 100%;
            height: 3px;
         }
         &::before {
            width: 3px;
            height: 100%;
         }
      }
      &__content {
         height: 0;
         opacity: 0;
         transition: all .3s ease;
         visibility: hidden;
      }
   }
}
