*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-style: normal;
	position: relative;
	&::after {
		content: "";
		position: fixed;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		width: 100vw;
		height: 100vh;
		background: #fff9f8;
		opacity: 1;
		transition: all 0.3s ease;
		visibility: visible;
	}
	&.loaded::after {
		opacity: 0;
		transition: all 0.3s ease;
		visibility: hidden;
	}
	&.locked {
		z-index: -1;
	}
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	@media (max-width: $md1) {
		max-width: 1200px;
	}
	@media (max-width: $md2) {
		max-width: 1100px;
	}
	@media (max-width: $md3) {
		max-width: 950px;
	}
	@media (max-width: $md4) {
		max-width: 750px;
	}
	@media (max-width: $md5) {
		max-width: none;
		padding: 0 25px;
	}
	@media (max-width: $md6) {
		padding: 0 15px;
	}
}
.section {
	@media (max-width: 768px) {
		padding: 70px 0;
	}
}

//main
.btn-bold {
	font-family: "Inter";
	font-weight: 600;
	font-size: 18px;
	line-height: 1.2;
	text-align: center;
	color: #ffffff;
	padding: 17px 40px;
	background: #7a5da8;
	border-radius: 50px;
	transition: all 0.3s ease;
	&:hover {
		transition: all 0.3s ease;
		color: #7a5da8;
		background: #ffffff;
	}
	@media (max-width: 600px) {
		font-size: 16px;
		padding: 15px 35px;
	}
}
.btn-arrow {
	display: flex;
	align-items: center;
	&-text {
		font-family: "Inter";
		font-style: normal;
		font-weight: 600;
		font-size: 18px;
		line-height: 1.22;
		color: #7a5da8;
		opacity: 0;
		visibility: hidden;
		margin-right: 5px;
		transition: all 0.3s ease;
		@media (max-width: 600px) {
			opacity: 1;
			visibility: visible;
			font-size: 16px;
		}
	}
	&:hover {
		.btn-arrow-text {
			opacity: 1;
			visibility: visible;
			transition: all 0.3s ease;
		}
	}
}
.btn-light {
	font-family: "Inter";
	font-weight: 600;
	font-size: 16px;
	line-height: 1.2;
	color: #7a5da8;
	background: #f7efff;
	border-radius: 50px;
	padding: 15px 38px;
	transition: all 0.3s ease;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	img {
		transform: scaleX(0);
		transform-origin: left;
		width: 0;
		margin-left: 5px;
		transition: all 0.3s ease;
	}
	&:hover {
		transition: all 0.3s ease;
		img {
			width: auto;
			transform: scaleX(1);
			transition: all 0.3s ease;
		}
	}
}
.main-swiper {
	max-width: 690px;
	margin: 0 !important;
	position: relative;
	.swiper-wrapper {
		border-radius: 50px 50px 0px 50px;
	}
	.slide {
		width: 100%;
		border-radius: 50px 50px 0px 50px;
		img {
			width: 100%;
			object-fit: cover;
			object-position: center center;
		}
	}
	&-control {
		position: absolute;
		bottom: 0;
		left: 0;
		background: #fff2ee;
		display: flex;
		align-items: center;
		z-index: 99;
		padding: 8px 16px;
	}
	.swiper-prev {
		margin-right: 0 !important;
	}
	&-btn {
		cursor: pointer;
	}
	&-pagination {
		user-select: none;
		cursor: default;
		margin: 0 19px;
		font-family: "Gilroy Light";
		font-style: normal;
		font-weight: 300;
		font-size: 24px;
		line-height: 1.2;
		color: #a89785;
		position: relative !important;
		bottom: 0 !important;
		right: 0 !important;
		@media (max-width: 600px) {
			font-size: 18px;
			margin: 0 10px;
		}
	}
	@media (max-width: 1280px) {
		max-width: 500px;
	}
	@media (max-width: 992px) {
		max-width: 600px;
		.swiper-wrapper {
			border-radius: 25px 0px 25px 25px;
		}
		img {
			border-radius: 25px 0px 25px 25px !important;
		}
		&-control {
			top: 0;
			bottom: unset;
		}
	}
	@media (max-width: 768px) {
		max-width: 100%;
	}
	@media (max-width: 600px) {
		.hero__swiper-btn {
			transform: scale(0.7);
		}
	}
}
.swiper-buttons {
	display: flex;
	align-items: center;
	.swiper-btn:first-child {
		margin-right: 16px;
	}
	@media (max-width: 768px) {
		display: none;
	}
}
.swiper-btn {
	cursor: pointer;
}
