	}
}
.link {
	font-family: "Inter";
	font-weight: 600;
	font-size: 16px;
	line-height: 1.2;
	color: #7a5da8;
}
a.link {
	transition: all 0.3s ease;
	&:hover {
		transition: all 0.3s ease;
		color: #594c4f;
	}
}
.text {
	font-family: "Inter";
