.section {
   &__reviews {
      padding: 120px 0;
      background: #fcfbfa;
   }
}
.reviews {
   &__item {
      margin-bottom: 80px;
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 auto 50px !important;
      .swiper-buttons{
         @media (max-width: 768px){
            display: none;
         }
      }
   }
   &__header-title {
      display: flex;
      align-items: center;
   }
   &__title {
      margin-bottom: 0 !important;
      margin-left: 10px;
   }
   &__swiper {
      max-width: 1920px;
      .swiper-slide {
         opacity: 0.5;
         @media (max-width: 768px){
            opacity: 1;
         }
         &-active {
            opacity: 1;
         }
      }
      &-google {
         .slide {
            background: linear-gradient(0deg, #ffffff, #ffffff), #fffbf6;
            box-shadow: 0px 0px 200px rgba(77, 63, 49, 0.03);
            border-radius: 40px 40px 40px 0px;
            padding: 50px;
            @media (max-width: 992px){
               padding: 25px;
               border-radius: 25px 25px 25px 0px;
            }
            @media (max-width: 768px){
               max-width: 400px;
            }
            @media (max-width: 480px){
               padding: 15px;
               max-width: 262px;
            }
            &__title {
               font-family: "Gilroy ExtraBold" !important;
               margin-bottom: 10px;
            }
            &__rate {
               margin-bottom: 20px;
            }
            &__text {
               color: #594c4f !important;
               margin-bottom: 20px;
               @media (max-width: 480px){
                  font-size: 15px !important;
                  margin-bottom: 10px;
               }
            }
            &__location {
               display: flex;
               align-items: center;
            }
            &__location-text {
               margin-left: 5px;
               color: #8B746D !important;
               @media (max-width: 480px){
                  margin-bottom: 10px;
               }
            }
         }
      }
      &-youtube{
         .slide{
            overflow: hidden;
            position: relative;
            border-radius: 25px 25px 25px 0px;
            @media (max-width: 768px){
               max-width: 263px;
               border-radius: 15px 15px 15px 0px;
            }
            img{
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
            &__content{
               left: 42px;
               bottom: 29px;
               position: absolute;
               display: flex;
               align-items: center;
               color: #FFFFFF !important;
               img{
                  margin-right: 10px;
                  width: auto;
                  height: auto;
               }
               @media (max-width: 1110px){
                  left: 20px;
                  bottom: 20px;
               }
               @media (max-width: 768px){
                  font-size: 13px !important;
                  left: 10px;
                  bottom: 15px;
                  img{
                     width: 20px;
                     margin-right: 5px;
                  }
               }
            }
         }
      }
   }
}
