.section {
   &__tech {
      padding: 0 0 120px;
   }
}
.tech {
   &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 55px;
      @media (max-width: 768px){
         flex-direction: column-reverse;
      }
   }
   &__img {
      width: 100%;
      img {
         width: 100%;
      }
   }
   &__content {
      margin-left: 118px;
      max-width: 572px;
      width: 100%;
      @media (max-width: 992px){
         margin-left: 30px;
      }
      @media (max-width: 768px){
         max-width: 100%;
         margin-left: 0;
         margin-bottom: 30px;
      }
   }
   &__title {
      margin-bottom: 30px !important;
      @media (max-width: 600px){
         margin-bottom: 15px !important;
      }
   }
   &__text {
      margin-bottom: 15px;
   }
   &__swiper {
      .slide {
         @media (max-width: 600px){
            max-width: 155px;
         }
         &__img {
            width: auto;
            margin-bottom: 27px;
            height: 66px;
            display: flex;
            align-items: center;
            @media (max-width: 600px){
               margin-bottom: 15px;
            }
            img {
               width: auto;
               object-fit: contain;
            }
         }
         &__title {
            margin-bottom: 10px;
            @media (max-width: 600px){
               margin-bottom: 5px;
            }
         }
      }
      .swiper-buttons {
         display: flex;
         justify-content: flex-end;
         margin-top: 50px;
         align-items: center;
         @media (max-width: 480px){
            display: none;
         }
      }
      .swiper-prev {
         margin-right: 16px;
      }
   }
}
