.section {
   &__about {
      padding: 120px 0;
      &-contact {
         .about {
            &__title {
               font-size: 24px !important;
               margin-bottom: 20px;
            }
            &__item {
               margin-bottom: 34px;
               &:last-child {
                  margin-bottom: 0;
               }
            }
            #moreBtn {
               display: none;
               cursor: pointer;
               @media (max-width: 600px) {
                  display: block;
               }
            }
            #more {
               @media (max-width: 600px) {
                  background: linear-gradient(180deg, #594c4f 0%, rgba(89, 76, 79, 0) 57.28%);
                  -webkit-background-clip: text;
                  -webkit-text-fill-color: transparent;
                  background-clip: text;
                  height: 110px;
                  text-fill-color: transparent;
                  &.active {
                     height: auto;
                     background: none;
                     -webkit-background-clip: unset;
                     -webkit-text-fill-color: unset;
                     background-clip: content-box;
                  }
               }
            }
         }
      }
      &-service {
         .about {
            max-width: 810px;
            width: 100%;
            margin: 0 auto;
            &__list {
               &-item {
                  margin-bottom: 10px;
                  display: flex;
                  align-items: flex-start;
                  &:last-child {
                     margin-bottom: 0;
                  }
               }
               &-num {
                  min-width: 25px;
                  max-width: 25px;
                  width: 100%;
                  height: 25px;
                  margin-top: -2px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  background: #ffffff;
                  border: 1px solid #fff2ee;
                  border-radius: 50%;
                  margin-right: 10px;
                  font-family: 'Gilroy ExtraBold' !important;
                  color: #8B746D !important;
               }
            }
         }
      }
   }
}
.about {
   &__text {
      font-weight: 300 !important;
      color: #333333 !important;
      margin-bottom: 15px;
      @media (max-width: 600px) {
         margin-bottom: 13px;
      }
      &:last-child {
         margin-bottom: 0;
      }
   }
}
