.section {
   &__online {
      padding: 80px 0 120px;
   }
}
.online {
   &__title {
   }
   &__steps {
      position: relative;
      max-width: 600px;
      width: 100%;
      z-index: 0;
      &::after {
         content: "";
         position: absolute;
         left: 50%;
         top: 0;
         bottom: 0;
         width: 1px;
         background: #fff2ee;
         transform: translate(-50%, 0);
         z-index: -1;
      }
      @media (max-width: 1110px){
         max-width: 100%;
         &::after{
            display: none;
         }
      }
   }
   &__steps-item {
      position: relative;
      display: flex;
      align-items: center;
      width: 50%;
      height: 55px;
      margin-bottom: 50px;
      &.right {
         margin-right: 0;
         margin-left: auto;
         .online__steps-num {
            left: 0%;
            right: auto;
            transform: translate(-50%, 0);
         }
         .online__steps-text {
            margin-left: 47px;
            margin-right: auto;
         }
      }
      &.complete {
         margin-bottom: 0;
         .online__steps-text {
            color: #7a5da8 !important;
            font-weight: 500 !important;
         }
         .online__steps-num {
            background: #fff2ee;
         }
      }
      @media (max-width: 1110px){
         width: 100%;
         margin-right: 0 !important;
         margin-left: 0 !important;
         flex-direction: row-reverse !important;
         justify-content: flex-start;
         height: auto;
         margin-bottom: 20px;
         &.right{
            margin-right: 0;
            margin-left: 0;
         }
      }
   }
   &__steps-text {
      margin-right: 47px;
      margin-left: auto;
      font-weight: 300 !important;
      @media (max-width: 1110px){
         margin-left: 14px !important;
         margin-right: auto !important;
      }
   }
   &__steps-num {
      background: #ffffff;
      border: 1px solid #fff2ee;
      width: 55px;
      height: 55px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      position: absolute;
      right: 0%;
      transform: translate(50%, 0);
      font-family: "Gilroy ExtraBold" !important;
      @media (max-width: 1110px){
         position: relative;
         left: 0;
         right: auto;
         transform: translate(0, 0) !important;
      }
      @media (max-width: 600px){
         width: 40px;
         height: 40px;
         img{
            transform: scale(.8);
         }
      }
   }
}
