.section {
   &__write {
      padding: 120px 0 0;
      @media (max-width: 992px){
         background: #fcfbfa;
         padding: 70px 0;
      }
   }
}
.write {
   background: #fcfbfa;
   border-radius: 30px 30px 30px 0px;
   padding: 78px 84px 83px 92px;
   @media (max-width: 1110px){
      padding: 60px 40px;
   }
   @media (max-width: 992px){
      padding: 0;
      border-radius: unset;
      background: none;
   }
   &__title {
      margin-bottom: 41px;
      @media (max-width: 600px){
         margin-bottom: 30px;
      }
   }
   &__form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
      @media (max-width: 1110px){
         gap: 20px;
      }
      @media (max-width: 768px){
         grid-template-columns: repeat(1, 1fr);
         gap: 15px;
      }
   }
   &__form-input {
      padding-bottom: 37px;
      border: none;
      background: none;
      border-bottom: 1px solid #DAD3CC;
      @media (max-width: 1110px){
         padding-bottom: 32px;
      }
   }
   &__form-textarea {
      resize: none;
      height: 199px;
      grid-row: 1 / 4;
      grid-column: 2 / 3;
      @media (max-width: 768px){
         grid-area: auto;
      }
   }
   &__form-btn {
      grid-column: 1 / 2;
      border: none;
      margin-right: auto;
      margin-left: 0;
      @media (max-width: 768px){
         margin-top: 15px;
         background: #7A5DA8 !important;
         color: #fff !important;
         cursor: pointer;
      }
      @media (max-width: 480px){
         width: 100%;
      }
   }
}
