.section {
   &__compare {
      padding: 100px 0 155px;
   }
}
.compare {
   &__text {
      color: #2e1a1e !important;
      width: 100%;
   }
   &__table-title {
      font-family: "Gilroy ExtraBold";
      font-size: 18px;
      line-height: 1.2;
      color: #2e1a1e;
      background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd;
      border: 1px solid #fff2ee;
      margin: 0 !important;
      padding: 18px 0;
      transition: all 0.3s ease;
      &:nth-child(2) {
         border-radius: 25px 0 0 0px;
      }
      &:last-child {
         border-radius: 0px 25px 0px 0px;
      }
      @media (max-width: 992px) {
         background: none;
         border: none;
         padding: 0;
         border-radius: 0;
         font-size: 16px;
         font-family: "Gilroy Light";
         cursor: pointer;
         padding-right: 24px;
         &.active {
            color: #7a5da8;
            font-family: "Gilroy ExtraBold";
         }
         &:last-child {
            margin-right: 0;
         }
      }
   }
   &__item-mark {
      margin-bottom: 18px;
      &:last-child {
         margin-bottom: 0;
      }
   }
   &__item-btn {
      border-radius: 0px 0px 25px 25px !important;
      padding: 14px 64px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      @media (max-width: 1280px) {
         padding: 14px 34px;
      }
      @media (max-width: 992px) {
         position: absolute;
         bottom: -80px;
         opacity: 1;
         visibility: visible;
         left: 0;
         border-radius: 25px !important;
      }
   }
   &__table {
      overflow: hidden;
      border-spacing: 1px !important;
      border-color: #fff2ee;
      @media (max-width: 992px) {
         display: none;
      }
      .hover {
         background: #f7efff;
         transition: all 0.3s ease;
         a {
            opacity: 1;
            visibility: visible;
            transition: all 0.3s ease;
         }
         &.compare__table-title {
            color: #7a5da8;
            transition: all 0.3s ease;
         }
      }
      tr td:first-child.hover, tr th:first-child{
         background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd !important;
      }
      tbody {
         padding: 30px 20px;
         background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd !important;
         border: 1px solid #fff2ee;
         border-radius: 20px 0px 20px 20px;
         td {
            margin: 0 !important;
            padding-bottom: 15px;
            &:last-child {
               padding-bottom: 0;
            }
            &.compare__table-img {
               position: relative;
               img {
                  position: absolute;
                  left: 50%;
                  transform: translate(-50%, 0);
               }
            }
         }
      }
      tfoot {
         height: 47px;
         td {
            border-radius: 0px 0px 25px 25px;
         }
      }
   }
   &__container {
      display: none;
      @media (max-width: 992px) {
         display: block;
      }
   }
   &__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      overflow-x: auto;
      margin-bottom: 21px;
      &::-webkit-scrollbar {
         height: 5px;
      }
      &::-webkit-scrollbar-thumb {
         background: #dad3cc;
         border-radius: 5px;
      }
   }
   &__items {
      display: flex;
      justify-content: space-between;
   }
   &__content {
      padding: 30px 20px;
      width: 100%;
      background: linear-gradient(0deg, #ffffff, #ffffff), #fafcfd;
      border: 1px solid #fff2ee;
      border-radius: 20px 0px 0px 20px;
      li {
         margin-bottom: 18px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
   &__items{
      position: relative;
   }
   &__item {
      display: none;
      background: #f7efff;
      border: 1px solid #fff2ee;
      padding: 30px 40px;
      border-radius: 0 0 20px 0;
      &.active {
         display: block;
      }
   }
}
